@import '../../styles/utils/mixins';
@import '../../styles/utils/variables';

#main {
  display: table;
  width: 100%;
  height: calc(100vh - 40px - 283px);
  text-align: center;
  background-color: #3b3e4a;

  @include onTablet {
    height: calc(100vh - 48px - 101px);
  }

  @include onDesktop {
    height: calc(100vh - 64px - 101px);
  }

  font-family: 'Lato', sans-serif;
  color: #f1f2f9;
  margin: 0;
}

.notfound {
  display: table-cell;
  vertical-align: middle;

  &__title {
    font-size: 25px;
    display: inline-block;
    padding-right: 12px;
  }
}
